import axios from 'axios';
import React from 'react'
import { useEffect } from 'react';
import { useState } from 'react';
import { useNavigate } from 'react-router-dom';


function Login({setUserDate}) {
  
  let [read,setRead] = useState({
    email:'',
    password:'',
    });

    let [loading,setLoading]=useState(false);
    let navigate = useNavigate();

    function gotohome(){
      let path='/home';
      navigate(path);
    }


   let onsubnet2 = async(e)=>{
    e.preventDefault();
    let {data} = await axios.post("https://knowledge-saraha.herokuapp.com/users/signIn",read);
    console.log(data);
    if(data.message=='login'){
      localStorage.setItem('token',data.token);
      setUserDate();
      gotohome();
    }
    //setLoginDate(true);
   }
   useEffect(()=>{
    if(localStorage.getItem('token')){
      setUserDate();
    }
   },[])

   let getvalue2 =(e)=>{
    let myread ={...read};
    myread[e.target.name] = e.target.value;
    setRead(myread);  
   }

  return (
    <>
    <form onSubmit={onsubnet2}>
      <div className='container pt-5'>  
        <div className="form-group mb-3">
          <label htmlFor="email" className='mb-3 fw-bold'>Email address :</label>
          <input type="email" onChange={getvalue2} className="form-control" name='email' id="email" aria-describedby="emailHelp" placeholder="Enter email" />
        </div>
        <div className="form-group mb-4">
          <label htmlFor="password" className='mb-3 fw-bold'>Password :</label>
          <input type="password" onChange={getvalue2} className="form-control" name='password' id="password" placeholder="Password" />
        </div>
        <button type="submit" className="btn btn-primary fw-bold">
        {loading?<i class=" fa-solid fa-spinner fa-spin"></i>:'login'}
        </button>
      </div>
    </form>
    </>
  )
}

export default Login