import axios from 'axios'
import React from 'react'
import {useEffect,useState} from 'react'
import styles from './Home.module.css'
import { useNavigate } from 'react-router-dom';


function Home() {
  let nav=useNavigate();
  function gotodetalis(id){
     nav({
      pathname:'/details',
      search:`?id${id}`
    })
  }
  let[trendin,setTrending]=useState([]);
  let [tvtren,setTvtren]=useState([]);
  let prefex="https://image.tmdb.org/t/p/w500/";
  async function Trendingmovie (method,callback){
    let {data} =await axios.get(`https://api.themoviedb.org/3/trending/${method}/day?api_key=3fbff3754e24e4cbe26eded5417a23c0`);
    callback(data.results);
  }
  useEffect(()=>{
    Trendingmovie("movie",setTrending);
    Trendingmovie("tv",setTvtren);
  },[])

  return (
    <>
    <div className='row pt-5 '>
      <div className="col-md-4 d-flex align-items-center">
        <div>
          <div className={`w-25 ${styles.brdr}`}></div>
          <h2 className='w-50' >Trending movies to watch now </h2>
          <p >most watched movies by day</p>
          <div className={`w-100 ${styles.brdr}`}></div>
        </div>
      </div>
      {trendin.map((movie,index)=>
      <div onClick={()=>gotodetalis(movie.id)} className="col-md-2" key={index}>
        <div  className=' pt-5'>
          <img className='w-75 text-center' src={prefex+movie.poster_path} alt="there are img" />
          <h2 className='w-100 mt-3 text-strat'>{movie.title}</h2>
        </div>
      </div>
      )}
    </div>
    <div className='row pt-5 '>
      <div className="col-md-4 d-flex align-items-center">
        <div>
          <div className={`w-25 ${styles.brdr}`}></div>
          <h2 className='w-50' >Trending tv to watch now </h2>
          <p >most watched movies by day</p>
          <div className={`w-100 ${styles.brdr}`}></div>
        </div>
      </div>
      {trendin.map((movie,index)=>
      <div className="col-md-2" key={index}>
        <div className=' pt-5'>
          <img className='w-75 text-center' src={prefex+movie.poster_path} alt="there are img" />
        </div>
      </div>
      )}
    </div>

    </>
  )
}
  

export default Home