import './App.css';
import Navbar from './components/Navbar/Navbar';
import Home from './components/Home/Home';
import Movies from './components/Movies/Movies';
import Login from './components/Login/Login';
import People from './components/People/People';
import Tvshow from './components/Tvshow/Tvshow';
import About from './components/About/About';
import Register from './components/Register/Register';
import { Navigate, Route, Routes } from 'react-router-dom';
import ProductRoutes from './components/ProductRoutes/ProductRoutes';
import  jwtDecode  from 'jwt-decode';
import { useState } from 'react';

function App() {
  let [loginDate , setLoginDate]=useState(null);

  function setUserDate(){
    let token =localStorage.getItem('token');
    let decode=jwtDecode(token);
    setLoginDate(decode);
   // console.log(loginDate);
  }
  function longout (){
    localStorage.removeItem('token');
    setLoginDate(null);
    Navigate('/login');
  }
  return (
    <div className=''>
      <Navbar loginDate={loginDate} logout={longout}/>
      <div className='container'>
        <Routes>
          <Route element={<ProductRoutes loginDate={loginDate}/>}>
            <Route path='home' element={<Home/>}></Route>
            <Route path='movies' element={<Movies/>}></Route>
          </Route>
          <Route path='tvshow' element={<Tvshow/>}></Route>
          <Route path='register' element={<Register/>}></Route>
          <Route path='about' element={<About/>}></Route>
          <Route path='login' element={<Login setUserDate={setUserDate}/>}></Route>
          <Route path='people' element={<People/>}></Route>
        </Routes>
      </div>
    </div>
    
  );
}

export default App;
