import axios from 'axios';
import Joi from 'joi';
import React from 'react'
import { useState } from 'react';
import { useNavigate } from 'react-router-dom';


 function Register() {

  let navigate = useNavigate();

  function gottologin(){
    let path='/login';
    navigate(path);
  }

  let [user,setUser] = useState({
    name: '',
    email:'',
    password:'',
    age:'',
  });

  let[errorlist,setErrorlist]=useState([]);

  let [loading,setLoading]=useState(false);

  let onsubnet = async (e)=>{
    e.preventDefault();
    let valedateresult =validetionform();
    //setErrorlist(valedateresult.error.details);
    let {data}=await axios.post("https://knowledge-saraha.herokuapp.com/users/signUp",user);
    //console.log(data);
    if(data.message =='success'){
      gottologin();
    }
    setLoading(true);
  }

  function validetionform(){
    const schema =Joi.object({
        name: Joi.string().alphanum().min(3).max(20),
        email: Joi.string().required().email({minDomainSegments:2, tlds:{ allow: ['com','net']}}),
        password: Joi.string().required(),
        age: Joi.number().required().min(20).max(70),
    });
    return schema.validate(user,{abortEarly:false});
  }
 

  let getvalue =(e)=>{
    let myuser={...user};// two postion in ram
    myuser[e.target.name] = e.target.value;
    setUser(myuser);  
    //console.log(myuser);
  }


  return (
    <>
    {errorlist.map((error,index)=>
      <div className='text-danger'>{error.message}</div>
    )}
    <form onSubmit={onsubnet}>
      <div className='container pt-5'>
        <div className="form-group mb-3">
          <label htmlFor="last-name" className='mb-3 fw-bold'> Name :</label>
          <input type="text" onChange={getvalue} className="form-control" name='name' id="name"  placeholder="Enter Name " />
        </div>
        
        <div className="form-group mb-3">
          <label htmlFor="age" className='mb-3 fw-bold'>Age :</label>
          <input type="number" onChange={getvalue} className="form-control" name='age' id="age"  placeholder="Enter The Age" />
        </div>
        <div className="form-group mb-3">
          <label htmlFor="email" className='mb-3 fw-bold'>Email address :</label>
          <input type="email" onChange={getvalue} className="form-control" name='email' id="email" aria-describedby="emailHelp" placeholder="Enter email" />
        </div>
        <div className="form-group mb-4">
          <label htmlFor="password" className='mb-3 fw-bold'>Password :</label>
          <input type="password" onChange={getvalue} className="form-control" name='password' id="password" placeholder="Password" />
        </div>
        <button type="submit" className="btn btn-primary fw-bold">
          {loading?<i class=" fa-solid fa-spinner fa-spin"></i>:'Register'}
          </button>
      </div> 
    </form>

    </>
  )
}

export default Register