import React from 'react'
import { Link } from 'react-router-dom'


function Navbar({loginDate ,logout}) {
  return (
    <>
  <nav className="navbar navbar-expand-lg ">
  <div className="container-fluid">
    <Link className="navbar-brand fw-bold text-light" href="#">Noxe</Link>
    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
      <span className="navbar-toggler-icon" />
    </button>
    <div className="collapse navbar-collapse" id="navbarSupportedContent">
        <ul className="navbar-nav  me-auto mb-2 mb-lg-0">
            <li className="nav-item">
            <Link className="nav-link text-light" aria-current="page" to="home">Home</Link>
          </li>
          <li className="nav-item ">
            <Link className="nav-link text-light" to="movies">Movies</Link>
          </li>
          <li className="nav-item">
            <Link className="nav-link text-light" to="tvshow" >Tvshow</Link>
          </li>
          <li className="nav-item">
            <Link className="nav-link text-light" to="people">People</Link>
          </li>
          <li className="nav-item">
            <Link className="nav-link text-light" to="about">About</Link>
          </li>
        </ul>
        <ul className="navbar-nav  ms-auto">
          {loginDate?<>
            <form className=" me-3" role="search">
              <input className="form-control " type="search" placeholder="Search" aria-label="Search" />
            </form>
            <li className='nav-item'>
              <Link className='nav-link text-light' to="/"><i class="fa-brands fa-facebook"></i></Link>
            </li>
            <li className='nav-item'>
              <Link className='nav-link text-light' to="/"><i class="fa-brands fa-spotify"></i></Link>
            </li>
            <li className='nav-item'>
              <Link className='nav-link text-light' to="/"><i class="fa-brands fa-instagram"></i></Link>
            </li>
            <li className='nav-item'>
              <Link className='nav-link text-light' to="/"><i class="fa-brands fa-youtube"></i></Link>
            </li>
            <li className="nav-item">
                <Link className="nav-link text-light"onClick={logout} >logout</Link>
              </li>
          </>:<>
          <form className=" me-3" role="search">
              <input className="form-control " type="search" placeholder="Search" aria-label="Search" />
            </form>
            <li className='nav-item'>
              <Link className='nav-link text-light' to="/"><i class="fa-brands fa-facebook"></i></Link>
            </li>
            <li className='nav-item'>
              <Link className='nav-link text-light' to="/"><i class="fa-brands fa-spotify"></i></Link>
            </li>
            <li className='nav-item'>
              <Link className='nav-link text-light' to="/"><i class="fa-brands fa-instagram"></i></Link>
            </li>
            <li className='nav-item'>
              <Link className='nav-link text-light' to="/"><i class="fa-brands fa-youtube"></i></Link>
            </li>
          <li className="nav-item">
              <Link className="nav-link  text-light " to="login">login</Link>
            </li>
          <li className="nav-item">
              <Link className="nav-link text-light" to="register">Register</Link>
            </li>
          
          </>}
        </ul>
    </div>
  </div>
</nav>

    </>
  )
}

export default Navbar